@import '~assets/styles/h3Imports';

.notification {
  border: 1px solid var(--interface-info, #284b9b);
  border-radius: var(--border-radius, 8px);
  box-shadow: var(--interface-info-shadow, 0 5px 14px rgba(40, 75, 155, 0.09));
  display: flex;
  margin-bottom: 26px;
  padding: 16px 20px;
}

.figure {
  flex: 0 1 32px;
  margin-right: 16px;
}

.notification-text {
  flex: 0 1 calc(100% - 48px);

  > *:last-child {
    margin-bottom: 0;
  }
}

.info {
  background-color: var(--interface-info-background, #f6f8fb);
  border: 1px solid var(--interface-info, #284b9b);
  box-shadow: var(--interface-info-shadow, 0 5px 14px rgba(40, 75, 155, 0.09));

  --toast-fill: var(--interface-info);
  --toast-stroke: var(--interface-info);
}

.success {
  background-color: var(--interface-success-background, #00853e);
  border-color: var(--interface-success, #00853e);
  box-shadow: var(--interface-error-shadow, 0 5px 14px rgba(225, 9, 0, 0.09));

  --toast-fill: var(--interface-success);
  --toast-stroke: var(--interface-success);
}

.warning {
  background-color: var(--interface-warning-background, #fffbf6);
  border-color: var(--interface-warning, #f26522);
  box-shadow: var(
    --interface-warning-shadow,
    0 5px 14px rgba(96, 101, 101, 0.09)
  );

  --toast-fill: var(--interface-warning);
  --toast-stroke: var(--interface-warning);
}

.error {
  background-color: var(--interface-error-background, #fef5f5);
  border-color: var(--interface-error, #e10900);
  box-shadow: var(--interface-error-shadow, 0 5px 14px rgba(225, 9, 0, 0.09));

  --toast-fill: var(--interface-error);
  --toast-stroke: var(--interface-error);
}

.title {
  font-size: rem(16);
  font-weight: bold;
  line-height: 1.25;
  margin: 7px 0 5px;
}

.text {
  font-size: rem(16);
  line-height: 1.5;
  margin-top: 0;
}
