@mixin text-label-normal {
    align-items: center;
    display: flex;
    font-size: rem(16);
    font-weight: 600;
    line-height: line-height(24, 16);
    width: fit-content;
}

@mixin text-label-disable {
    color: var(--input-disabled, #A5AAAA);
}

@mixin text-label-error {
    color: var(--radio-button-error,#E10900);
}