html {
  --primary-button: var(--white);
  --primary-button-background: var(--brand-secondary, #284b9b);
  --primary-button-background-hover: #3e417a;
  --primary-button-background-pressed: #222346;
  --secondary-button: var(--brand-secondary, #284b9b);
  --secondary-button-background: #c9e7fc;
  --secondary-button-background-hover: #dff0fd;
  --secondary-button-background-pressed: #a1d2f5;
  --tertiary-button: var(--brand-secondary, #284b9b);
  --tertiary-button-background: var(--brand-secondary, #284b9b);
  --tertiary-button-background-hover: #dde3ee;
  --tertiary-button-background-pressed: #bbc7dd;
  --delete-button: var(--brand-primary, #e62600);
  --delete-button-background: var(--brand-primary, #e62600);
  --delete-button-background-hover: #fae1e0;
  --delete-button-hover: #aa0a0a;
  --delete-button-background-pressed: #f6c4c3;
  --disabled-button: var(--white);
  --disabled-button-background: var(--gray-2);
  --focus-button-outline-color: #5995b3;
}
