@import '../../assets/styles/h3Imports';

html {
  --search-width: rem(100);
  --search-height: rem(48);
}

.search {
  display: inline-block;
  vertical-align: middle;

  .button {
    background-color: white;
    border: none;
    border-radius: rem(8) 0 0 rem(8);
    border-right: rem(1) solid var(--accent-interactive);
    min-height: rem(48);
    min-width: rem(48);
    vertical-align: middle;

    --icon-stroke: var(--gray-3);
  }

  .input {
    @include interactive-text;

    border: none;
    font-weight: 600;
    max-width: var(--search-width);
    min-height: var(--search-height);
    min-width: var(--search-width);
    padding: rem(12);
    vertical-align: middle;

    &::-webkit-search-cancel-button {
      display: none;
    }

    &:focus-visible {
      outline: none;
    }

    &[data-size='small'] {
      --search-width: 12.437rem; // 199px
    }

    &[data-size='medium'] {
      --search-width: 13.5625rem; // 217px
    }

    &[data-size='large'] {
      --search-width: 17.875rem; // 286px
    }

    &[data-size='extra-large'] {
      --search-width: 18.625rem; // 298px
    }
  }

  .close {
    background-color: white;
    border-radius: 0 rem(8) rem(8) 0;
    display: inline-block;
    padding: rem(4);
    vertical-align: middle;
  }
}

[data-open='false'] {
  display: none;
}
