@import '~assets/styles/h3Imports';

.modal-overlay {
  background: var(--modal-overlay-background);
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;

  --modal-spacing: var(--spacing-xs);
  --buttons-padding-small: 14px 15px;
  --bordered-buttons-padding-small: 13px 16px;
  @media (min-width: 696px) {
    --modal-spacing: var(--spacing-m);
  }

  @media (#{$large-up}) {
    --modal-spacing: var(--spacing-m);
  }

  --button-min-width: 208px;
}

.modal-window {
  background: #ffffff;
  border-radius: 24px;
  box-shadow: var(--box-shadow-modal);
  margin: rem(50) var(--spacing-xs) auto;
  max-width: rem(472);
  padding: var(--modal-spacing);
  position: relative;

  @media (min-width: 696px) {
    margin: rem(50) auto auto;
  }

  @media #{$large-up} {
    max-width: rem(664);
  }
}

.header-image {
  display: flex;
  justify-content: center;
  padding-top: var(--spacing-l);

  @media #{$tablet-up} {
    padding-top: var(--spacing-xxxxs);
  }
}

.heading {
  @include h2;

  margin-top: 0;
  padding-right: 70px;
}

.heading-with-image {
  margin-top: var(--spacing-l);
  padding-right: 0;
  text-align: center;
}

.close-button {
  @include interactive-text;

  align-items: center;
  background: transparent;
  border: none;
  color: var(--close-button-color);
  display: flex;
  line-height: 1;
  min-height: 24px;
  min-width: 70px;
  padding: 0;
  position: absolute;
  right: var(--modal-spacing);

  &:focus-visible {
    outline: 2px solid var(--focus-button-outline-color, #5995b3);
  }
}

.close-svg {
  margin-right: var(--spacing-xxxs);
}

.buttons-container {
  margin-top: var(--spacing-m);
}

.buttons {
  display: flex;
  justify-content: center;

  @media #{$small-down} {
    flex-direction: column;

    button,
    a {
      font-size: rem(18);
      height: auto;
      margin-bottom: 0;
      margin-right: 0;
      max-height: 48px;

      &:first-child {
        margin-bottom: var(--spacing-xs);
      }
    }
  }

  @media #{$tablet-up} {
    button,
    a {
      margin-bottom: 0;
    }
  }
}

.buttons-desktop {
  display: none;

  &-all {
    display: flex;
  }
  @media #{$tablet-up} {
    display: flex;
  }
}

.buttons-mobile {
  @media #{$tablet-up} {
    display: none;
  }
}
