@import '~assets/styles/h3Imports';

.header {
  background-color: var(--brand-secondary);
  color: var(--white);
  display: flex;
  justify-content: space-between;
  margin: auto;
  max-width: 100vw;
  min-height: rem(64);
  padding: rem(8);

  a {
    color: var(--header-links);
    margin: rem(10);
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

    &[aria-current='page'] {
      font-weight: bold;
    }
  }

  .left,
  .right {
    align-items: center;
    display: inline-flex;
  }

  .right {
    margin-left: auto;
  }
}

.language-button {
  background: none;
  border: none;
}

.active-language {
  font-weight: bold;
}
