@import '~assets/styles/h3Imports';

.svg-wrapper {
  display: inline-block;
}

svg {
  transition: all 0.25s ease;

  rect,
  path {
    transition: inherit;
  }

  .active &,
  a:hover &,
  a:focus & {
    --icon-background: var(--icon-background-hover, #c2e8ff);
  }
}

[aria-current='page'] {
  --icon-background: var(--icon-background-hover, #c2e8ff);
}
